.example {
  max-width: 1100px;
  margin-bottom: 3rem;
  margin-left: auto;
  margin-right: auto;
}

.container {
  position: relative;
}

.caroucssel:before {
  content: "";
  speak: never;
  scroll-snap-align: start;
}

.item {
  padding: 2rem 1rem;
  font-family: Pacifico, cursive;
  font-size: 1.75rem;
}

.item:nth-child(3n) {
  background: #cddc39;
}

.item:nth-child(3n-1) {
  background: #e91e63;
}

.item:nth-child(3n-2) {
  background: #00bcd4;
}

.item-label {
  font-family: Vollkorn, serif;
  font-size: 1rem;
  display: block;
}

.button {
  opacity: 1;
  color: #fff;
  text-shadow: 0 0 12px #2f2f2fbf;
  width: 2rem;
  margin: -1.25rem 0 0;
  padding: 1.25rem 0;
  font-family: icons;
  font-size: 3rem;
  font-weight: 400;
  transition: opacity .3s, text-shadow .3s;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}

.button:focus {
  outline: none;
}

.button[disabled] {
  opacity: .25;
}

.button span {
  display: none;
}

.is-previous {
  left: 0;
}

.is-previous:before {
  content: "";
}

.is-next {
  right: 0;
}

.is-next:before {
  content: "";
}

.pagination {
  text-align: center;
  margin-top: .5rem;
}

.pagination li {
  margin: 0 .25rem;
