  color: #fff;
  background: #2f2f2f;
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
  font-family: Pacifico, cursive;
  line-height: 1.5;
  transition: background .3s, color .3s;
  display: block;
  position: relative;
  overflow: hidden;
}

.pagination button:hover, .pagination button:focus {
  outline: none;
}

.pagination button[disabled] {
  color: #2f2f2f;
  background: #fff;
  border-radius: 50%;
}
